import axios from "axios";
import { useState } from "react";
import { Link, useOutletContext } from "react-router-dom";


export default function Cart() {
    const { cartData, getCart } = useOutletContext(); //useOutletContextfrontlayoutcartData
    const [loadingItems, setLoadingItems] = useState([]);
    const [couponCode, setCouponCode] = useState("");//

    const removeCartItem = async (id) => {
        try {
            const res = await axios.delete(`/v2/api/${process.env.REACT_APP_API_PATH}/cart/${id}`);
            getCart();//
            console.log("", res);
        } catch (error) {
            console.log(error);
        }
    }

    const updateCartItem = async (item, quantity) => {
        const data = {
            "data": {
                "product_id": item.product_id,
                "qty": quantity,
            },
        };
        setLoadingItems([...loadingItems, item.id])
        try {
            const res = await axios.put(`/v2/api/${process.env.REACT_APP_API_PATH}/cart/${item.id}`, data);
            getCart();
            console.log("", res);
            setLoadingItems(loadingItems.filter((loadingObject) => loadingObject !== item.id),);
        } catch (error) {
            console.log(error);
        }
    }

    const applyCoupon = async () => {
        try {
            const res = await axios.post(`/v2/api/${process.env.REACT_APP_API_PATH}/coupon`, {
                data: { code: couponCode }
            });
            getCart(); // 
            alert("");
        } catch (error) {
            alert("");
        }
    };


    return (<>
        <div className="container">
            <div className="row justify-content-center">
                <div className="col-md-6 bg-white py-5" style={{ minHeight: "calc(100vh - 56px - 76px)" }}>
                    <div className="d-flex justify-content-between">
                        <h2 className="mt-2"></h2>
                    </div>
                    <div className="row">
                        <div className="col-md-7">
                            <input type="text" className="form-control rounded-0 py-3 w-100" placeholder=""
                                value={couponCode}
                                onChange={(e) => setCouponCode(e.target.value)} />
                        </div>
                        <div className="col-md-5">
                            <button className="btn btn-dark btn-block rounded-0 py-3 w-100" type="button" onClick={applyCoupon} disabled={!couponCode}></button>
                        </div>
                    </div>
                    {cartData?.carts?.length > 0 ? (<>
                        {cartData?.carts?.map((item) => {
                            return (
                                <div className="d-flex mt-4 bg-light" key={item.id}>
                                    <img src={item.product.imageUrl} alt="" style={{ width: "120px" }} className="object-cover" />
                                    <div className="w-100 p-3 position-relative">
                                        <button
                                            type="button"
                                            className="position-absolute btn"
                                            style={{ top: "12px", right: "16px", }}
                                            onClick={() => removeCartItem(item.id)}>
                                            <i className="bi bi-x"></i></button>
                                        <p className="mb-0 fw-bold">{item.product.title}</p>
                                        <p className="mb-1 text-muted" style={{ fontSize: "14px" }}>{item.product.description}</p>
                                        <div className="d-flex justify-content-between align-items-center w-100">
                                            <div className="input-group w-50 align-items-center">
                                                <select name="" className="form-select" id=""
                                                    value={item.qty}//select  item.qty 
                                                    disabled={loadingItems.includes(item.id)}
                                                    onChange={
                                                        (e) => {
                                                            updateCartItem(item, e.target.value * 1); //*1 onChange e.target.value 
                                                        }
                                                    }>
                                                    {
                                                        [...(new Array(20))].map((i, num) => {
                                                            return (
                                                                <option value={num + 1} key={num}>{num + 1}</option>
                                                            )
                                                        })
                                                    }
                                                </select>
                                            </div>
                                            <p className="mb-0 ms-auto">NT${item.total}</p>
                                        </div>
                                    </div>
                                </div>
                            )
                        })}

                        <table className="table mt-4 text-muted">
                            <tbody>
                                <tr>
                                    <th scope="row" className="border-0 px-0 font-weight-normal"></th>
                                    <td className="text-end border-0 px-0">NT$ {cartData.total}</td>
                                </tr>
                                <tr>
                                    <th scope="row" className="border-0 px-0 pt-0 font-weight-normal"></th>
                                    <td className="text-end border-0 px-0 pt-0">-NT${cartData.total - cartData.final_total}</td>
                                </tr>
                                <tr>
                                    <th scope="row" className="border-0 px-0 pt-0 font-weight-normal"></th>
                                    <td className="text-end border-0 px-0 pt-0">+NT$160</td>
                                </tr>
                            </tbody>
                        </table>
                        <div className="d-flex justify-content-between mt-4">
                            <p className="mb-0 h4 fw-bold"></p>
                            <p className="mb-0 h4 fw-bold">NT${Math.ceil(cartData.final_total + 160)}</p>
                        </div>
                        <Link to="../checkout" className="btn btn-dark btn-block mt-4 rounded-0 py-3 w-100"></Link></>)
                        : (<div className="text-center mt-5">
                            <h4 className="mb-3"></h4>
                            <Link to="/products" className="btn btn-dark btn-block mt-4 rounded-0 py-3 px-5">
                                
                            </Link>
                        </div>)}
                </div>
            </div>
        </div >
    </>)
}