/*

1.Context
2.reducer action 
3.initialState */

import { createContext } from "react";// React  createContext  Context 

//useContext
export const MessageContext = createContext({});// useContext(MessageContext)  context
export const inistate = {
    type: '',
    title: '',
    text: ''
};


//Reducer
export const messageReducer = (state, action) => { //state action
    switch (action.type) {//action.typeMessage.js
        case "POST_MESSAGE": // "POST_MESSAGE"  action
            return{
                ...action.payload, //productModals
            };
        case "CLEAR_MESSAGE": // "CLEAR_MESSAGE"
            return{
                ...inistate,
            }
        default:
            return state;  // action type  state
            
    }
}

export function handleSuccessMessage(dispatch, res) { //ProductModal()Storeexport
    dispatch({
        type: 'POST_MESSAGE', //
        payload: {
            type: 'success',
            title: '',
            text: res.data.message,
        },
    });
    setTimeout(()=>{
        dispatch({
            type:'CLEAR_MESSAGE',
        });
    },3000);
}

export function handleErrorMessage(dispatch, error) {
    dispatch({
        type: 'POST_MESSAGE', //
        payload: {
            type: 'danger',
            title: '',
            text: Array.isArray(error?.response?.data?.message) ? error?.response?.data?.message.join('') : error?.response?.data?.message,
            //isArrayjoin,message?. (optional chaining)  error  response  data  undefined
        },
    });
    setTimeout(()=>{
        dispatch({
            type:'CLEAR_MESSAGE',
        });
    },3000);
}