import axios from "axios";
import { useEffect, useState,useContext } from "react";
import { MessageContext, handleSuccessMessage, handleErrorMessage } from "../store/messageStore";


export default function CouponModal({ closeModal, getCoupons, type, tempCoupon }) {
    const [tempData, setTempData] = useState({
        title: "",
        is_enabled: 1,
        percent: 80,
        due_date: 1555459200,
        code: 'testCode',
    });
    const [, dispatch] = useContext(MessageContext)

    //input(2025-04-25)new Dateapi(unix timestamp)
    const [date, setDate] = useState(new Date());
    console.log(date);

    useEffect(() => {
        if (type === 'create') {
            setTempData({
                title: "",
                is_enabled: 1,
                percent: 80,
                due_date: 1555459200,
                code: 'testCode',
            });
            setDate(new Date());//
        } else if (type === 'edit') {
            setTempData(tempCoupon);
            setDate(new Date(tempCoupon.due_date));//
        }
    }, [type, tempCoupon]);// type  tempCoupon 

    const handleChange = (e) => { //onChangehandleChange(e) e.target  name  valuesetTempData
        console.log(e);
        const { value, name } = e.target; //e.targetname()value()
        if (['price', 'origin_price', 'percent'].includes(name)) { //name
             if (isNaN(value) || value.trim() === "") return; // 
            setTempData({
                ...tempData,
                [name]: Number(value)//
            });
        } else if (name === 'is_enabled') {
            setTempData({
                ...tempData,
                [name]: +e.target.checked,//e.target.checked+true1false0
            })
        } else {
            setTempData({
                ...tempData,
                [name]: value
            })
        }
    };

    const submit = async () => {//tempData axios  API
        try {
            let api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/coupon`
            let method = 'post';
            if (type === 'edit') {
                api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/coupon/${tempCoupon.id}`;
                method = 'put';
            }//submitapi
            const res = await axios[method](
                api,
                {
                    data: {
                        ...tempData,
                        due_date: date.getTime(),//newDateunix timestamp
                    },
                });//api data tempData
            console.log(res);
            handleSuccessMessage(dispatch, res);
            closeModal();//submitmodal
            getCoupons();//
        } catch (error) {
            console.log(error);
            handleErrorMessage(dispatch, error);
        }
    };

    return (
        <div className="modal fade" id="productModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div className="modal-dialog modal-lg">
                <div className="modal-content">
                    <div className="modal-header">
                        <h1 className="modal-title fs-5" id="exampleModalLabel">
                            {type === 'create' ? '' : `${tempData.title}`}
                        </h1>
                        <button type="button" className="btn-close" aria-label="Close" onClick={closeModal}></button>
                        {/*data-bs-dismissonClick={closeModal} */}
                    </div>
                    <div className="modal-body">
                        <div className='mb-2'>
                            <label className='w-100' htmlFor='title'>
                                
                                <input
                                    type='text'
                                    id='title'
                                    placeholder=''
                                    name='title'
                                    className='form-control mt-1'
                                    value={tempData.title}
                                    onChange={handleChange}
                                />
                            </label>
                        </div>
                        <div className='row'>
                            <div className='col-md-6 mb-2'>
                                <label className='w-100' htmlFor='percent'>
                                    %
                                    <input
                                        type='text'
                                        name='percent'
                                        id='percent'
                                        placeholder='%'
                                        className='form-control mt-1'
                                        value={tempData.percent}
                                        onChange={handleChange}
                                    />
                                </label>
                            </div>
                            <div className='col-md-6 mb-2'>
                                <label className='w-100' htmlFor='due_date'>
                                    
                                    <input
                                        type='date'
                                        id='due_date'
                                        name='due_date'
                                        placeholder=''
                                        className='form-control mt-1'
                                        value={`${date.getFullYear().toString()}-${(date.getMonth() + 1) //getMonth1
                                            .toString()
                                            .padStart(2, 0)}-${date //(targetLength)0(padString)
                                                .getDate()
                                                .toString()
                                                .padStart(2, 0)}`}
                                        onChange={(e) => {
                                            console.log(e.target.value);
                                            setDate(new Date(e.target.value));//new Date
                                        }}
                                    />
                                </label>
                            </div>
                            <div className='col-md-6 mb-2'>
                                <label className='w-100' htmlFor='code'>
                                    
                                    <input
                                        type='text'
                                        id='code'
                                        name='code'
                                        placeholder=''
                                        className='form-control mt-1'
                                        value={tempData.code}
                                        onChange={handleChange}
                                    />
                                </label>
                            </div>
                        </div>
                        <label className='form-check-label' htmlFor='is_enabled'>
                            <input
                                className='form-check-input me-2'
                                type='checkbox'
                                id='is_enabled'
                                name='is_enabled'
                                checked={!!tempData.is_enabled}
                                onChange={handleChange}
                            />
                            
                        </label>
                    </div>
                    <div className="modal-footer">
                        <button type="button" className="btn btn-secondary" onClick={closeModal} ></button>
                        {/*data-bs-dismissonClick={closeModal} */}
                        <button type="button" className="btn btn-primary" onClick={submit}></button>
                    </div>
                </div>
            </div>
        </div >
    )
}