import { useEffect, useRef, useState } from "react";
import axios from "axios";
import ProductModal from "../../components/ProductModal";
import DeleteModal from "../../components/DeleteModal";
import Pagination from "../../components/Pagination";
import { Modal } from "bootstrap"; // Modal


export default function AdminProducts() {
    const [products, setProducts] = useState([]);
    const [pagination, setPagination] = useState({});
    //type:modal,createedit
    const [type, setType] = useState('create');
    const [tempProduct, setTempProduct] = useState({});//

    const productModal = useRef(null);// ref() null({current:null})
    const deleteModal = useRef(null);

    useEffect(() => {
        productModal.current = new Modal('#productModal', {// id Modal  useRef (productModal)
            backdrop: 'static',//
            keyboard: false    //esc
        });
        deleteModal.current = new Modal('#deleteModal', {
            backdrop: 'static',
            keyboard: false
        });
        getProducts();
    }, []);

    const getProducts = async (page = 1) => { //page1
        const productRes = await axios.get(`/v2/api/${process.env.REACT_APP_API_PATH}/admin/products?page=${page}`);//
        console.log("", productRes);
        setProducts(productRes.data.products);
        setPagination(productRes.data.pagination);
    }

    const openProductModal = (type, product) => {
        setType(type);
        setTempProduct(product);//productproductsetTempProducttempProducttempProducttempProduct
        productModal.current.show();
    }
    const closeProducModal = () => {//boostsrapjs
        productModal.current.hide();
    }

    const openDeleteModal = (product) => {
        setTempProduct(product);//
        deleteModal.current.show();
    }
    const closeDeleteModal = () => {
        deleteModal.current.hide();
    }
    const deleteProduct = async (id) => {
        try {
            const res = await axios.delete(`/v2/api/${process.env.REACT_APP_API_PATH}/admin/product/${id}`);
            if (res.data.success) {//
                getProducts();//
                deleteModal.current.hide();//Modal
            }
        } catch (error) {
            console.log(error)
        }
    }

    return (<>
        <div className="p-3">
            <ProductModal
                closeProducModal={closeProducModal}
                getProducts={getProducts}
                tempProduct={tempProduct}
                type={type}
            />
            <DeleteModal
                close={closeDeleteModal}
                text={tempProduct.title}
                handleDelete={deleteProduct}
                id={tempProduct.id}
            />
            <h3></h3>
            <hr />
            <div className="text-end">
                <button
                    type="button"
                    className="btn btn-primary btn-sm"
                    onClick={() => openProductModal('create', {})}//clickopenProductModalopenProductModal()
                >
                    
                </button>
            </div>
            <table className="table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                    </tr>
                </thead>
                <tbody>
                    {products.map((product) => {
                        return (
                            <tr key={product.id}>
                                <td>{product.category}</td>
                                <td>{product.title}</td>
                                <td>{product.price}</td>
                                <td>{product.is_enabled ? "" : ""}</td>
                                <td>
                                    <button
                                        type="button"
                                        className="btn btn-primary btn-sm"
                                        onClick={() => openProductModal('edit', product)}
                                    >
                                        
                                    </button>
                                    <button
                                        type="button"
                                        className="btn btn-outline-danger btn-sm ms-2"
                                        onClick={() => openDeleteModal(product)}
                                    >
                                        
                                    </button>
                                </td>
                            </tr>
                        )

                    })}
                </tbody>
            </table>
            <Pagination pagination={pagination} changePage={getProducts} />
        </div>
    </>)
}